import Common from '@/lib/common'
export default {
  data(){
    return{
      dealStatus:"",
      taxStatus:"",
      payWrongText:"",
      taxWrongText:"",
      paySuccessColor:"red",
      taxSuccessColor:"red"
    }
  },
  mounted(){
    var that =this;
    Common.getPayStatus(function(res){
      if(res.data.code!="200"){
        that.dealStatus="";
        that.taxStatus="";
        that.payWrongText="";
        that.taxWrongText="";
      }else{
        if(res.data.context[0]==''){
          that.dealStatus="";
          that.taxStatus="";
          that.payWrongText="";
          that.taxWrongText="";
        }else{
          if(res.data.context[0].xtZxbz=="0" && res.data.context[0].status=="0"){
            that.dealStatus="";
            that.paySuccessColor="green"
            that.payWrongText="";
          }
        }
        if(res.data.context[1]==''){
          that.taxStatus="";
          that.taxWrongText="";
        }else{
          if(res.data.context[1].procResult=="000"){
            that.taxStatus="";
            that.taxWrongText="";
            that.taxSuccessColor="green";
          }else{
            that.taxStatus="";
            that.taxWrongText=''+res.data.context[1].procResult + ',' +res.data.context[1].errTex;
            that.taxSuccessColor="red";
          }
        }
      }
    });
  },
  methods: {
    goback1() {
      this.$router.push("/index")
    },
    loadAlert(){
      Common.loading(this,'');
    }
  }

}



